.App {
  text-align: center;
  align-content: top;
  max-width: 688px;
  margin-left: auto;
  margin-right: auto;
  height: 100vh;
}

.App-header {
  position: fixed;
  top: 0;
  left: 0;
  padding-top: 1%;
  padding-bottom: 1%;
  padding-left: 3%;
  padding-right: 3%;
  width: min-content;
  background-color: white;
  border: black 1px solid;
  height: min-content;
  z-index: 1;
}

.App-jobContent {
  margin-top: calc(max(4vh, 4vmin));
}

.App-jobName select {
  font-size: calc(min(10px + 2vmin, 18px));
  font-weight: bold;
}

.App-totalJobPoints {
  font-size: calc(min(10px + 2vmin, 18px));
}

.App-jobOption {
  width: 12vmin;
  margin: auto;
  margin-bottom: 2vmin;
  aspect-ratio: 1;
  border: solid 2px black;
}

.App-jobList {
  margin: auto;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}